import loginTemp from '@/components/login'
import inputCell from '@/components/input-cell'
import { forgetPwd } from '@/api'
import { mapActions } from 'vuex'
import { verifyPhoneNumber, dealCountDown } from '@/common/js/common.js'
export default {
	name: '',
	data () {
		return {
			phone: '',
			sms: '',
			pwd: '',
			count: 0
		}
	},
	onLoad () {

	},
	methods: {
		...mapActions(['reqGetSms']),
		async onGetSmsEvent () {
			let that = this;
			if(!verifyPhoneNumber(that.phone)) {
				return;
			}
			that.reqGetSms({phone: that.phone, callback: dealCountDown, that: that})
		},
		async onChangeEvent () {
			let that = this;
			if(!verifyPhoneNumber(that.phone)) {
				return;
			}
			if(this.sms == '') {
				uni.showToast({
					title: '请输入验证码',
					icon: 'none'
				})
				return
			}
			if(this.pwd == '') {
				uni.showToast({
					title: '请输入密码',
					icon: 'none'
				})
				return
			}
			let result = await forgetPwd({
				mobile: this.phone,
				captcha: this.sms,
				newpassword: this.pwd
			})
			if(result.code == 200) {
				uni.showToast({
					title: '修改成功'
				})
				setTimeout(() => {
					uni.reLaunch({
						url: '/pages/login/login'
					})
				}, 2000)
			}
		}
	},
	components: {
		loginTemp,
		inputCell
	}
}
